.Movie__root {
  padding: 16px;
  position: relative;
  .Movie__img {
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.25);
  }
  .Movie__cover {
    width: 100%;

    position: absolute;
    bottom: 0;
    left: 0;

    overflow: hidden;
    background-color: #ffffff;
    box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.25);
    height: 120px;
    padding: 16px;
  }
  .Movie__genres {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 8px;
  }
}
