html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #eeeeee;
  font-family: "Roboto", sans-serif;
}

h3 {
  font-size: 16px;
  padding: 0px;
  margin: 0px;
}
ul,
ol,
li {
  margin: 0px;
  padding: 0px;
  font-size: 12px;
  color: #777777;
  list-style: none;
}
